/* Options navigation menu */
.rml-options {
    border-bottom: 5px solid #e1e1e1;
    border-left: 5px solid #e1e1e1;

    nav {
        background: url(../images/icon.png) no-repeat 10px center;
        padding: 15px 5px 0px 70px;
        border-bottom: 1px solid #e1e1e1;

        ul {
            padding: 0px;
            margin: 0px;

            &:after {
                clear: both;
                display: block;
                content: " ";
            }
        }
    }

    .nav-tab {
        margin-bottom: -1px;
        cursor: pointer;
    }

    > table {
        display: none;
        margin: 0px;

        td,
        th {
            padding: 17px 20px;
        }
    }
}
