/* Media modal Lite version (FolderSelector */
.rml-attachment-filter-folder-selector {
    margin-top: 32px;
    margin-right: 2%;
    display: inline-block;
    vertical-align: middle;
}

/* Media modal */
.media-frame.rml-media-modal {
    /* Some plugins hide menu */
    .media-frame-menu {
        display: block !important;
        width: 270px;
    }

    &:not(.hide-menu) {
        .media-frame-title,
        .media-frame-router,
        .media-frame-content,
        .media-frame-toolbar {
            left: 270px;
        }
    }

    &.hide-menu .media-frame-menu {
        left: -270px !important;
    }

    .rml-modal-container {
        .aiot-pad {
            margin: 0;
            padding: 0;

            > div:nth-child(1) {
                /* Toolbar */
                padding: 0 20px;
            }

            > div:nth-child(2) {
                /* Nodes */
                padding: 0 20px;
            }

            > div {
                display: block !important;
            }

            .aiot-tree-headline {
                padding-top: 0;
            }
        }
    }

    &.rml-mobile-modal .media-menu {
        overflow-y: auto;
    }

    &:not(.rml-mobile-modal) {
        .media-menu {
            display: flex;
            flex-direction: column;
            margin-bottom: -10px;
        }

        .rml-modal-container {
            flex: 1 1 auto;
            position: relative;

            > .aiot-tree {
                position: static;

                > .aiot-pad {
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    display: flex;
                    flex-direction: column;

                    > div:nth-child(2) {
                        flex: 1 1 auto;
                        overflow: auto;
                    }
                }
            }
        }
    }
}

.rml-webkit .media-frame.rml-media-modal:not(.rml-mobile-modal) .rml-modal-container .aiot-pad > div:nth-child(2) {
    overflow-y: overlay;
}

tr.compat-field-rml_shortcut {
    height: 1px;
    padding: 0;
}
