/* View in attachments details */
.rml-deactivated {
    opacity: 0.8;
    pointer-events: none !important;
}

.rml-folder-edit {
    display: inline-block;
    border: 1px solid #ddd;
    -webkit-box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.07);
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.07);
    background-color: #fff;
    color: #32373c;
    outline: 0;
    -webkit-transition: 0.05s border-color ease-in-out;
    transition: 0.05s border-color ease-in-out;
    position: relative;
    padding: 0px 10px;

    &:hover {
        color: white;
        background: #0073aa;
        cursor: pointer;
    }

    > div {
        padding: 5px;
    }
}

/* SVG support in list table */
body.wp-admin.upload-php .has-media-icon img[src$=".svg"] {
    width: 60px;
    height: 60px;
}

/* Shortcut info container */
.rml-shortcut-info-container {
    margin-bottom: 15px;
    padding-bottom: 15px;

    h2 {
        font-weight: 600;
        text-transform: uppercase;
        font-size: 12px;
        color: #666;
        margin: 0 0 10px 0;
    }
}

/* Shortcut icon with tooltip in grid mode */
body.upload-php ul.attachments {
    padding-bottom: 8px !important;
}

.rml-shortcut-grid .rml-shortcut-container {
    position: absolute;
    bottom: -10px;
    left: -10px;
    padding: 3px 3px 4px 4px;
    color: #0073aa;
    -webkit-transition: all 250ms ease;
    -moz-transition: all 250ms ease;
    -o-transition: all 250ms ease;
    transition: all 250ms ease;
    border-radius: 14px;
    font-size: 11px;
    border: 1px solid transparent;
    text-align: left;
}

.media-frame.mode-grid .rml-shortcut-grid:focus .rml-shortcut-container,
.media-frame.mode-grid .selected .rml-shortcut-container {
    bottom: 0;
    left: 0;
    border-radius: 0px 14px 0px 0px;
}

/* Shortcut icon with tooltip in table mode */
.rmlShortcutSpan {
    display: none;
}
