.aiot-wp-dark-mode {
    .rml-postbox {
        border: 1px solid #1b2127;
        color: white;
        background: #32373c;

        h2 {
            border-bottom-color: #1c2228;
        }
    }

    .rml-folder-edit {
        background: #23282d;
        color: white;
        border-color: #1d2228;
    }

    .ant-modal-content,
    .ant-modal-header,
    .ant-modal-title,
    .ant-modal-footer {
        background: #32373c;
        color: white;
        border-color: #1d2228;
    }
}
