/* Meta box */
.rml-postbox {
    position: relative;
    border: 1px solid #e5e5e5;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
    background: #fff;
    margin-bottom: 15px;

    > h2 {
        font-size: 14px;
        padding: 8px 12px;
        margin: 0;
        line-height: 1.4;
        border-bottom: 1px solid #eee;
    }

    > ul {
        background: #d86767;
        color: white;
        padding: 5px 11px !important;
    }

    > .inside {
        padding: 8px 12px;
    }

    form {
        > h3 {
            margin: 30px 0 5px 0;
            text-transform: uppercase;
            font-size: 12px;
            color: #777777;

            &:first-of-type {
                margin-top: 0;
            }
        }

        > label {
            font-weight: bold;
            display: block;
            margin-top: 10px;
        }

        .regular-text {
            max-width: 100%;
            box-sizing: border-box;
        }
    }
}

.ant-modal .rml-postbox {
    border: 0 !important;
    box-shadow: none !important;
    margin: 0 !important;

    > .inside {
        padding: 0 !important;
    }
}

.rml-drop-zone {
    margin: 5px 0;
    padding: 10px;
    text-align: center;
    background: #dceaf0;
}
