html[dir="rtl"] {
    .ant-message,
    .ant-popover,
    .ant-dropdown,
    .ant-dropdown-menu-submenu-popup {
        text-align: right;
    }

    .rml-media-modal.media-frame:not(.hide-menu) .media-frame-title,
    .rml-media-modal.media-frame:not(.hide-menu) .media-frame-router,
    .rml-media-modal.media-frame:not(.hide-menu) .media-frame-content,
    .rml-media-modal.media-frame:not(.hide-menu) .media-frame-toolbar {
        left: 0;
        right: 270px;
    }

    @media (max-width: 900px) {
        .rml-media-modal.media-frame:not(.hide-menu) .media-frame-content,
        .rml-media-modal.media-frame:not(.hide-menu) .media-frame-router,
        .rml-media-modal.media-frame:not(.hide-menu) .media-frame-title,
        .rml-media-modal.media-frame:not(.hide-menu) .media-frame-toolbar {
            right: 0 !important;
        }
    }
}
